<template>
<!--扫描组件-->
<div class="scan" @touchstart.prevent="scanQRCodeView">
	<span v-if="sourceType=='repair'" class="repairscan">
		<i class="icon iconfont icon-saoma-"></i>
		扫码定位设备
	</span>
	<div v-else-if="sourceType=='spitslot'" class="spitslotscan">
		<i class="icon iconfont icon-saoma-"></i>
		<span>扫码定位</span>
	</div>
</div>
</template>
<script type="ecmascript-6">
import {
	scanQRCode
} from 'api/global'
export default {
	name: 'scan',
	props: ['sourceType'],
	methods: {
		scanQRCodeView() {
			//扫描二维码点击
			// console.log('扫描二维码');
			scanQRCode().then(res => {
				let v = +new Date()
//			let locationHref = `${process.env.BASE_URL}/${this.sourceType}?projectId=${res.projectId}&type=${res.type}&spaceId=${res.spaceId}&v=${v}`
				const urls=`${this.sourceType}?projectId=${res.projectId}&type=${res.type}&spaceId=${res.spaceId}&v=${v}`
				window.location.replace(urls)
			}).catch(err => {
				console.error(err)
			})
		},
	}
}
</script>
<style scoped lang="less">
/*转换rem*/

@rem: 30rem;
.scan {
	width: 100%;
	height: 125/@rem;
	display: flex;
	justify-content: space-between;
	align-items: center;
	.icon-saomiao {
		color: #5d5d5d;
		font-size: 46/@rem;
		vertical-align: -2px;
	}
	i {
		display: inline-block;
		/*font-size:40/@rem;*/
		margin-right: 30/@rem;
	}
	span {
		flex: 4;
		font-size: 32/@rem;
		i {
			margin-left: 30/@rem;
			margin-right: 10/@rem;
			vertical-align: -3/@rem;
		}
	}
	.spitslotscan {
		width: 100%;
		height: 75/@rem;
		margin-top: -50/@rem;
		text-align: center;
		i {
			margin-left: 30/@rem;
			color: #44B197;
			font-size: 58/@rem;
		}
		span {
			display: inline-block;
			font-size: 20/@rem;
			font-family: "pingfangSC-regular";
			color: #6D6D6D;
			padding-top: 6/@rem;
		}
	}
	.repairscan{
		background-color: #f4f4f6;
		color:#44B197;
		font-size: 28/@rem;
		vertical-align: baseline;
		padding-right:30/@rem;
		i{
			color:#44B197;
			vertical-align: baseline;
			font-size: 28/@rem;
		}
	} 
}
</style>


