<template>
<!--填写报修信息页-->
<div class="repair">
	<div class="getPositionLoading" v-show="getPositionLoading">
		<mt-spinner type="fading-circle" color="#fff" :size="80"></mt-spinner>
	</div>
	<ul class="repairMes">
		<!--报修信息-->
		<li @touchstart.prevent="getBuildingMes">
			<span>
    				<em>*</em>
    				<i class="icon iconfont icon-building"></i>
    				报修建筑
    			</span>
			<span :class="{noData:selectBuilding=='请先选择建筑'}">
				{{selectBuilding}}
			</span>
			<span>
				<i class="icon iconfont icon-dayuhao"></i>
			</span>
		</li>

		<li class="splitHead">
			<h4>
				报修详情
			</h4>
			<div class="scan_code">
				<scan-code :sourceType="sourceType"></scan-code>
			</div>
		</li>

		<li @touchstart.prevent="getPlaceData">
			<span>
				<em>*</em>
				<i class="icon iconfont icon-icon"></i>
				报修地点
			</span>
			<span :class="{noData:repairPlace=='请选择报修问题所在的地点'}">
				{{repairPlace}}
			</span>
			<span>
				<i class="icon iconfont icon-dayuhao"></i>
			</span>
		</li>

		<transition name="equipment">
			<li @touchstart.prevent="getEquipmentData" v-show="isEquipmentShow">
				<span>
						<em></em>
	    				<i class="icon iconfont icon-baoxiushebei"></i>
	    				报修设备
	    			</span>
				<span :class="{noData:repairEquipment=='请选择报修的设备(选填)'}">
	    				{{repairEquipment}}
	    			</span>
				<span>
	    				<i class="icon iconfont icon-dayuhao"></i>
	    			</span>
			</li>
		</transition>

		<li @touchstart.prevent="getIssueData">
			<span>
    				<em>*</em>
    				<i class="icon iconfont icon-issue"></i>
    				报修问题
    			</span>
			<span :class="{noData:repairIssue=='请尽量选择报修类型'}">
    				{{repairIssue}}
    			</span>
			<span>
    				<i class="icon iconfont icon-dayuhao"></i>
    			</span>
		</li>
		<li>
			<span>
    				<em>*</em>
    				<i class="icon iconfont icon-dianhua"></i>
    				联系方式
    			</span>
			<span>
    				<input 
    					type="number" 
    					v-model.number="userPhone" 
    					placeholder="请填写您的手机号"/>
    			</span>
		</li>
	</ul>
	<div class="remarks">
		<evaluate-input :placeholders="placeholders" :picArr="picArr" @limitinput="limitinput">
			<h4 slot="remark">添加备注</h4>
		</evaluate-input>
	</div>
	<div class="submitRepair">
		<!--提交报修-->
		<input :class="submitclass" type="button" value="提交报修" @click.prevent="submitRepairMes" />
	</div>
	<dia-log :renderArr="renderArr" :isDialogShow="isDialogShow" @renderCont="renderCont" @closeDialog="closeDialog"></dia-log>
</div>
</template>
<script type="ecmascript-6">
import {
	Toast,
	MessageBox,
	Spinner
} from 'mint-ui'
import diaLog from './diaLog'
import coordtransform from 'coordtransform'
import scanCode from 'views/repairs/common/scan'
import evaluateInput from '../common/evaluateInput'
import {
	mapGetters,
	mapActions
} from 'vuex'
// import queryString from 'query-string'
import tools from 'utils/tools'
import {
	getBuildingArr, //获取建筑列表
	getRepairPlaceArr, //获取报修地点的数据列表
	getEquipmentArr,
	getRepairTypeArr, //获取报修问题的数据列表
	setRepair //提交报修
} from 'api/repair'
import {
	weChatReady,
	getLocation,
	closeWindow
} from 'api/global'
export default {
	components: {
		'scan-code': scanCode,
		'dia-log': diaLog,
		'evaluate-input': evaluateInput
	},
	data() {
		return {
			//展示部分
			placeholders: '建议填写设备问题的描述信息，以便我们提供更好的服务。',
			repairPlace: '请选择报修问题所在的地点', //报修地点
			repairIssue: '请尽量选择报修类型', //报修问题
			repairEquipment: '请选择报修的设备(选填)', //报修设备
			userPhone: "", //联系电话
			limitInput: '', //用户输入的备注信息
			picArr: [], //图片的数组
			getPositionLoading: true, //控制loading展示的

			//数据交互部分
			isDialogShow: false, //dialog是否展示
			renderArr: null, //渲染数据的数组，选择地点时，是地点的数组，选择问题时是问题的数组
			isEquipmentShow: false,
			locationRes: null, //获取到的建筑信息
			placeId: 0, //保存地点id，通过地点获取
			repairId: 0, //保存地点id，通过地点获取
			equipmentId: 0, //保存设备id，通过地点获取
			currentPlace: null,
			sourceType: 'repair',
			buildingArr: null,
			reSubmit: false, //这个用来控制多次点击时，会造成重复提交

			//最终提交的数据
			mesForAdd: {
				"projectId": 0, //buildingMes.pid
				"repairId": 0, //报修的问题的id
				"phone": 0, //联系人电话
				"sites": [{
					"id": 0,
					"objects": []
				}],
				"remark": [{
					"name": "1", //文字类型
					"value": "" //位置内容
				}]
			}
		}
	},
	computed: {
		...mapGetters([
			'buildingMes', 'upload_token'
		]),
		submitclass() {
			if (this.repairId && this.placeId && this.buildingMes.id && this.userPhone != '') {
				return "submit"
			}
			return "cannotsubmit"
		},
		selectBuilding() {
			if (this.buildingMes) {
				//获取到的信息里包含distance信息
				return this.buildingMes.name
			} else {
				return '请先选择建筑'
			}
		},
	},
	methods: {
		...mapActions(['GetRepairPlaceArr', 'SelectBuildingAndRepair', 'SaveBuildingMes', 'SaveBuildingArr']),
		limitinput(val) {
			this.limitInput = val
		},
		// putImgs(val){
		// 	this.picArr.push(val)
		// 	alert(JSON.stringify(this.picArr))
		// },
		verifyPhone() {
			//验证电话号
			if (this.userPhone == '') {
				return
			}
			tools.verifyPhone(this.userPhone) || Toast('电话号码格式不正确')
		},
		// fetchData(){
		getqueryString() {
			if (!!location.search) {
				const parsedObj = tools.queryString(location.search)
				if (!!parsedObj && !!parsedObj.projectId && !!parsedObj.spaceId) {
					this.SelectBuildingAndRepair(parsedObj).then(res => {
						this.repairPlace = `${res.leftVal} - ${res.rightVal}`
						getEquipmentArr(res.insetId).then(equipRes => {
							//如果长度存在，证明能够选择设备，就让选择设备显示出来
							this.repairEquipment = ''
							this.isEquipmentShow = !!equipRes.data.Content.length
						})
						//保存id到要添加的数据
						this.placeId = res.insetId
					}).catch(err => {
						Toast(err)
					})
				}
			}
		},
		submitRepairMes() {
			//点击提交报修
			if (this.repairId && this.placeId && this.buildingMes.id) {
				//保存用户输入信息
				if (this.userPhone == '') {
					return Toast('请输入电话号')
				}
				//  else if (!tools.verifyPhone(this.userPhone)) {
				// 	return Toast('输入电话号码格式不正确')
				// }
				this.mesForAdd.projectId = this.buildingMes.id //建筑id
				this.mesForAdd.repairId = this.repairId //报修问题的id
				this.mesForAdd.sites[0].id = this.placeId //地点id
				this.mesForAdd.sites[0].objects[0] = {
					id: this.equipmentId //报修设备Id
				}
				this.mesForAdd.phone = this.userPhone
				this.mesForAdd.remark[0].value = this.limitInput
				let successCount = 0

				if (this.reSubmit) {
					return Toast('请勿重复提交')
				}
				this.reSubmit = true
				if (this.picArr.length > 0) {
					for (let picItem of this.picArr) {
						tools.putb64(this.upload_token, picItem).then(picRes => {
							successCount++
							this.mesForAdd.remark.push({
								name: '2',
								value: picRes
							})
							if (successCount == this.picArr.length) {
								this.setRepairForAdd()
							}
						}).catch(picErr => {
							alert(picErr)
						})
					}
				} else {
					this.setRepairForAdd()
				}
				console.log(JSON.stringify(this.mesForAdd))
			} else {
				Toast('有必填项没有填写')
			}
		},
		setRepairForAdd() {
			setRepair(this.mesForAdd).then(res => {
				if (res.data.Result == "success") {
					this.$router.replace({
						name: 'repairResult'
					})
				}
			}).catch(err => {
				this.$router.replace({name:'nofound'})
				console.error(err)
			})
		},
		getBuildingMes() {
			//跳转到选择建筑页面
			//如果赋值成功，说明带距离信息的列表拿到了
			this.$router.push({
				name: 'selectBuilding',
				params: {
					buildingArr: this.buildingArr,
					repair: true
				}
			})
		},
		getPlaceData() {
			this.isDialogShow = true
			//获取建筑id
			let id = this.buildingMes.id
			//通过建筑id获取位置信息
			this.GetRepairPlaceArr(id).then(res => {
				this.renderArr = res.renderArr
				//在dialog页，用repairType作为区分要渲染的东西
				this.renderArr.repairType = res.repairType
				//左右的描述信息
				this.renderArr.left = res.left
				this.renderArr.right = res.right
			}).catch(err => {
				console.error(err)
			})
		},
		getIssueData() {
			//弹窗显示
			this.isDialogShow = true
			//获取pid
			let id = this.buildingMes.pid
			//通过pid获取数据
			getRepairTypeArr(id).then(res => {
				if (res.data.Content.length && res.data.Result == "success") {
					this.renderArr = res.data.Content.map(item => {
						return {
							id: item.id,
							name: item.name,
							child: item.items
						}
					})
					//在dialog页，用repairType作为区分要渲染的东西
					this.renderArr.repairType = "issue"
					//左右的描述信息
					this.renderArr.left = '类别'
					this.renderArr.right = '报修项目'
				}
			}).catch(err => {
				console.error(err)
			})
		},
		getEquipmentData() {
			//获取设备的信息，根据选择地点之后的的id获取
			this.isDialogShow = true
			//获取pid
			let id = this.placeId
			//通过pid获取数据
			getEquipmentArr(id).then(res => {
				if (res.data.Result == "success") {
					this.renderArr = res.data.Content.map(item => {
						return {
							id: item.id,
							name: item.name,
							child: item.equipments
						}
					})
					this.renderArr.repairType = "equipments"
					this.renderArr.left = '类别'
					this.renderArr.right = '设备'
				}
			})
		},
		renderCont(obj) {
			//拿到要渲染和提交的地点信息
			if (this.renderArr.repairType == "place") {
				//拼接要显示的地点信息
				this.repairPlace = `${obj.leftVal} - ${obj.rightVal}`
				getEquipmentArr(obj.insetId).then(res => {
					//如果长度存在，证明能够选择设备，就让选择设备显示出来
					this.repairEquipment = ''
					this.isEquipmentShow = !!res.data.Content.length
				})
				//保存id到要添加的数据
				this.placeId = obj.insetId
			} else if (this.renderArr.repairType == "issue") {
				//报修问题要显示的信息
				this.repairIssue = `${obj.leftVal} - ${obj.rightVal}`
				//保存repairId到要添加的数据
				this.repairId = obj.insetId
			} else if (this.renderArr.repairType == "equipments") {
				//报修设备要显示的信息
				this.repairEquipment = `${obj.leftVal} - ${obj.rightVal}`
				//保存添加的设备的id
				this.equipmentId = obj.insetId
			}
		},
		closeDialog() {
			this.isDialogShow = false
		},
		roundDistance(tempBuildArr, result) {
			let fromLatitude = null,
				fromLongitude = null,
				toLatitude = null,
				toLongitude = null
			if (!!result && !!result.latitude) {
				//fromLatitude：用户地理位置信息中的纬度
				fromLatitude = Number.parseFloat(result.latitude)
			}
			if (!!result && !!result.longitude) {
				//fromLongitude：用户地理位置信息中的经度
				fromLongitude = Number.parseFloat(result.longitude)
			}
			tempBuildArr = tempBuildArr.map((eachBuild) => {
				let distance = null
				if (!!fromLatitude && !!fromLongitude) {
					//如果用户的经度和纬度存在，那么走进这里
					let tempDistance = eachBuild.distance || ''
					let distanceArr = tempDistance.split(',') || []
					//distanceArr的格式：['116.289548','40.052366']值是后台传过来的
					if (distanceArr.length > 1) {
						toLatitude = parseFloat(distanceArr[1]) //后台传过来的建筑位置的纬度
						toLongitude = parseFloat(distanceArr[0]) //后台传过来的经度
					}
					let bd09togcj02 = coordtransform.bd09togcj02(toLongitude, toLatitude)
					toLatitude = bd09togcj02[1]
					toLongitude = bd09togcj02[0]
					//fromLatitude,fromLongitude,toLatitude,toLongitude
					//用户的纬度，用户的经度，建筑的纬度，建筑的经度
					distance = tools.getDistance(fromLatitude, fromLongitude, toLatitude, toLongitude)
				}
				let rstItem = {
					id: eachBuild.id,
					sign: eachBuild.sign,
					name: eachBuild.name,
					distance: distance,
					password: eachBuild.password,
					repairstatus: eachBuild.repairstatus,
					pid: eachBuild.pid
				}
				return rstItem
			})
			tempBuildArr.sort((a, b) => {
				if (!!a.distance && !!b.distance) {
					return Number.parseFloat(a.distance) - Number.parseFloat(b.distance)
				}
			})
			return tempBuildArr
		},
		fetchLocation(buildingArray, locationRes) {
			//关闭loading
			this.getPositionLoading = false
			//重整数组，将距离计算出来，返回依据距离排序过后的[{distance:100,id:}]或：[distance:null,id:]
			this.buildingArr = this.roundDistance(buildingArray, locationRes)
			//将数据缓存到vuex
			this.SaveBuildingArr(this.buildingArr)
			//拿到第一个，如果有距离，那么第一个是最近的
			var closestInfo = this.buildingArr[0]
			if (JSON.stringify(locationRes) != '{}' && closestInfo.distance < 500) {
				//如果获取到了用户位置信息，且距离小于500
				//将信息保存到buildingMes
				this.SaveBuildingMes(closestInfo)
			}
		},
		getDistances() {
			//获取用户位置信息
			return new Promise((resolve, reject) => {
				weChatReady().then(() => {
					getLocation().then(locationRes => {
						resolve(locationRes)
					}).catch(err => { //获取位置信息失败
						reject('getLocation' + err)
					})
				}).catch(err => {
					reject('weChatReady' + err)
				})
			})
		},
		getBuildingArrMes() {
			//获取建筑列表信息
			return new Promise((resolve, reject) => {
				getBuildingArr().then(res => {
					var buildingArray = res.data.Content || []
					resolve(buildingArray)
				}).catch(readyErr => {
					rejcet(readyErr)
				})
			})
		},
		fetchBuildingMes() {
			this.getBuildingArrMes().then(buildingArrs => { //获取建筑信息
				this.getDistances().then(distance => { //获取当前位置信息
					this.fetchLocation(buildingArrs, distance)
				}).catch(err => {
					//没能获取到位置信息
					this.fetchLocation(buildingArrs, {})
					Toast('暂时无法获取您的位置,请手动选择位置报修')
				})
			})
		}
	},
	created() {
		document.title = "报修"
		this.reSubmit = false
		if (!!location.search) {
			//如果链接存在，说明扫码进来的，解析url，拿到信息
			this.getqueryString()
			//关闭loading
			this.getPositionLoading = false
			this.fetchBuildingMes()
		} else if (!!this.$route.params && !!this.$route.params.repair) {
			//如果是从选择建筑跳转过来的
			this.getPositionLoading = false
			this.getPlaceData()
		} else {
			//如果连接不存在，且不是从选择建筑进来的
			//移除缓存的buildingMes
			tools.rmStorage('buildingMes')
			this.fetchBuildingMes()
		}
	}
}
</script>
<style scoped lang="less">
@rem: 30rem;
@lh: 110/@rem;

/*loading动画*/

.repair {
	height: 100%;
}

.getPositionLoading {
	position: absolute;
	left: 0;
	top: 0;
	bottom: 0;
	right: 0;
	z-index: 2;
	display: flex;
	overflow: hidden;
	background-color: rgba(0, 0, 0, 0.5);
	span {
		margin: 400/@rem auto;
	}
}


/*报修信息*/

.repairMes {
	background-color: #f4f4f6;
	li {
		height: @lh;
		background-color: #fff;
		line-height: @lh;
		overflow: hidden;
		border-bottom: 1px solid #ecf2f4;
		display: flex;
		align-items: center;
		span:nth-of-type(1) {
			display: inline-block;
			vertical-align: baseline;
			width: 246/@rem;
			height: @lh;
			line-height: @lh;
			color: #9597a0;
			font-size: 30/@rem;
			i {
				display: inline-block;
				margin: 0 10/@rem;
				line-height: @lh;
				vertical-align: baseline;
				color: #d1d5db;
			}
			em {
				display: inline-block;
				width: 9/@rem;
				margin-left: 20/@rem;
				line-height: @lh;
				vertical-align: baseline;
				/*vertical-align: -3/@rem;*/
				color: #44b197;
			}
		}
		span:nth-of-type(2) {
			display: inline-block;
			width: 460/@rem;
			height: @lh;
			line-height: @lh;
			white-space: nowrap;
			text-overflow: ellipsis;
			overflow: hidden;
			input {
				width: 100%;
				height: 115/@rem;
				border: none;
				padding-left: 9/@rem;
				vertical-align: baseline;
			}
			input::-webkit-input-placeholder {
				vertical-align: baseline;
				font-size: 30/@rem;
				color: #9597a0;
			}
			@media (min-device-width: 375px) and (max-device-width: 667px) and (-webkit-min-device-pixel-ratio: 2) {
				/*iphone 6*/
				input {
					width: 100%;
					height: @lh;
					border: none;
					vertical-align: baseline;
					padding-left: 9/@rem;
					font-size: 30/@rem;
				}
				input::-webkit-input-placeholder {
					height: @lh;
					line-height: 38/@rem;
					vertical-align: baseline;
					font-size: 30/@rem;
					color: #9597a0;
				}
			}
			@media (min-device-width: 414px) and (max-device-width: 736px) and (-webkit-min-device-pixel-ratio: 3) {
				/*iphone 6 plus*/
				input::-webkit-input-placeholder {
					height: @lh;
					line-height: 30/@rem;
					padding-bottom: 40/@rem;
					font-size: 32/@rem;
					color: #9597a0;
				}
			}
		}
		span:nth-of-type(3) {
			height: @lh;
			line-height: @lh;
			i {
				color: #b4b4b4;
				font-size: 24/@rem;
			}
		}
		.noData {
			color: #9597a0;
			font-size: 30/@rem;
		}
	}
	li:nth-of-type(1),
	li:nth-of-type(2) {
		border-bottom: none;
		height: 100/@rem;
	}
	li:nth-of-type(2) {
		justify-content: space-between;
		padding-top: 30/@rem;
	}
	/*报修详情*/
	.splitHead {
		height: 100/@rem;
		background-color: #f4f4f6;
		h4 {
			height: 68/@rem;
			line-height: 68/@rem;
			font-size: 26/@rem;
			padding-left: 40/@rem;
			color: #929499;
		}
		/*扫描二维码*/
		.scan_code {
			height: 68/@rem;
			overflow: hidden;
			background-color: #fff;
			border-bottom: 1px solid #ecf2f4;
			.scan {
				height: 68/@rem;
			}
		}
	}
	.equipment-enter-active,
	.equipment-leave-active {
		transition: all 0.5s ease-out;
	}
	.equipment-enter,
	.equipment-leave-active {
		height: 0;
		opacity: 0;
	}
}

.remarks {
	padding-bottom: @lh + 10/@rem;
}

.submitRepair {
	position: fixed;
	bottom: 0;
	width: 100%;
	height: 100/@rem;
	input {
		border: none;
		width: 100%;
		height: 100%;
		font-size: 34/@rem;
	}
	.submit {
		color: #fff;
		background-color: #44b197;
	}
	.cannotsubmit {
		color: #fff;
		background-color: #8e8f99;
	}
}
</style>


